import { User } from '../model/user.model';

import { Injectable } from '@angular/core';

@Injectable({providedIn: 'root'})
export class UserService{
    private users: User[] = [];

    getUsers(){
        return [...this.users];
    }

    addUser(username: String, name: String){
        const user: User = {
            userName: username,
            name: name
        }
        this.users.push(user);
    }
}